.courseTime img{
    width: 8px;
    height: 8px;
}

.allcourcesContainer2{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 40px;
}

.courseTime{
    display: inline-flex;
    flex-direction: row;
    gap: 1px;
    align-items: center;
}

@media (min-width: 300px){
    .courseTitle {
        font-family: 'Clash Display';
        font-style: normal;
        font-weight: 500;
        letter-spacing: 0.8px;
        font-size: 11px;
        width: 107%;
        line-height: 15px;
        margin-top: 21px;
    }

    .CoursesComponent {
        color: white;
        background: #1E1E1E;
        border-radius: 10px;
        padding: 20px;
        width: 204px;
        height: 338px;
        margin-bottom: 30px;
    }

    .CoursesComponentImg {
        width: 207px;
        height: 137px;
        border-radius: 10px;
        margin: 0 auto;
    }

    .CoursesComponent-row2 {
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        gap: 6px;
        font-family: 'Poppins';
        font-style: normal;
        font-weight: 500;
        font-size: 7px;
        margin-top: 10px;
    }


    .courseTime {
        display: inline-flex;
        flex-direction: row;
        gap: 1px;
    }

    .courseSpec {
        background: #F9B70B;
        border-radius: 5px;
        padding: 3px 15px 3px 15px;
    }

    /*.courseTitle {*/
    /*    font-family: 'Clash Display';*/
    /*    font-style: normal;*/
    /*    font-weight: 500;*/
    /*    font-size: 24px;*/
    /*    line-height: 30px;*/
    /*    margin-top: 21px;*/
    /*}*/

    .coursePrice {
        margin-top: 18px;
        font-style: normal;
        font-weight: 500;
        font-size: 13px;
        line-height: 30px;
        color: #D1CACA;
    }

    .courseCTA {
        background: #FFFFFF;
        border-radius: 10px;
        border: none;
        padding: 4px 56px;
        font-family: 'Clash Display';
        font-style: normal;
        font-weight: 500;
        font-size: 21px;
        line-height: 34px;
        color: #1E1E1E;
        margin-top: 22px;
        text-align: center;
        width: 200px;
        height: 38px;
    }
}

